* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: none;
}

body {
  background:
    linear-gradient(to bottom, rgba(255, 255, 255, 0.15) 0%, rgba(0, 0, 0, 0.15) 100%),
    radial-gradient(at top center, rgba(255, 255, 255, 0.40) 0%, rgba(0, 0, 0, 0.40) 120%) #989898;
  background-blend-mode: multiply, multiply;
  height: 100%;
  width: 100%;

  .header_desk {
    height: 60px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    background: #353433;
    padding: 0px 20px;

    .add_sticker {
      height: 34px;
      padding: 5px 20px;
      cursor: pointer;
      background: none;
      text-transform: uppercase;
      color: #fff;
      border: 1px solid #afafaf;
      border-radius: 4px;
      transition: .2s all ease-in-out;
      font-family: 'Staatliches', cursive;
      letter-spacing: 1.4px;
      font-size: 16px;

      &:hover {
        background: #afafaf;
        color: #353433;

        &:active {
          background: darken(#afafaf, 8%);
        }
      }
    }
  }

  .form {
    height: calc(100vh - 60px);
    transition: .1s all ease-in-out;
    position: relative;

    .sticker {
      display: flex;
      flex-direction: column;
      min-height: 200px;
      min-width: 200px;
      box-shadow: 0 6px 15px #24252666;
      border-radius: 16px;
      background: #353433;
      position: absolute;
      margin: 20px 20px;
      z-index: 200;

      .button-group {
        display: flex;
        justify-content: space-between;
        padding: 0 10px;
        padding-top: 6px;
        align-items: baseline;
        position: relative;

        span {
          font-family: 'Staatliches', cursive;
          color: #fff;
          letter-spacing: 1.4px;
        }

        .fa-dot-circle {
          cursor: move;
        }

        .fa-plus {
          cursor: pointer;
        }

        button {
          background: none;
          border: none;
          height: 25px;
          width: 25px;
          opacity: .4;
          transition: .3s all ease-in-out;

          &:hover {
            opacity: .8;
          }

          i {
            color: #fff;
            font-size: 16px;
          }

          .fa-edit {
            position: absolute;
            left: 175px;
            top: 8px;
          }
        }
      }

      textarea {
        min-height: 200px;
        min-width: 200px;
        margin: 8px;
        border-radius: 16px;
        padding: 16px;
        font-size: 16px;
        font-family: 'Oswald', sans-serif;
      }
    }
  }
}

.fa-plus {
  transform: rotate(45deg);
}

.fadeIn {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #353433;
  opacity: 0.8;
}

.modal {
  display: flex;
  flex-direction: column;
  min-height: 200px;
  min-width: 200px;
  box-shadow: 0px 2px 8px #e4e4e4;
  border-radius: 16px;
  background: #353433;
  position: absolute;
  top: calc(50% - 200px);
  left: calc(50% - 100px);
  margin: 20px 20px;
  z-index: 300;

  .fields {
    display: flex;
    flex-direction: column;
    padding: 20px;
    padding-bottom: 0px;

    span {
      margin-top: 5px;
      color:#ce4f4f;
      font-size: 12px;
      text-transform: lowercase;
    }

    label {
      font-family: 'Staatliches', cursive;
      letter-spacing: .6px;
      padding-bottom: 5px;
      color: #fff;

      &:last-of-type {
        margin-top: 20px;
      }
    }

    input {
      border: 0;
      height: 24px;
      outline: 0;
      border-radius: 4px;
      min-width: 200px;
      font-family: 'Oswald', sans-serif;
      padding: 5px;
    }

    textarea {
      border: 0;
      outline: 0;
      border-radius: 4px;
      min-width: 200px;
      font-family: 'Oswald', sans-serif;
      min-height: 150px;
      padding: 5px;
    }
  }

  .btnWrapper {
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20px 0px;

    button {
      height: 24px;
      width: 100px;
      border:none;
      outline: none;
      color: #fff;
      text-transform: uppercase;
      cursor: pointer;
      font-family: 'Staatliches', cursive;
      font-size: 14px;
      border-radius: 4px;
      &:disabled{
        background: #a1a1a1;
      }
      &:first-child{
        background: #85bb60;
      }
      &:last-child{
        background: #a32929;
      }
    }
  }
}